button {
  position: relative;
  height: 65px;
  width: 80px;
  color: #F8F8F8;
  outline: 1px solid #F8F8F8;
  border: none;
  background-color: #222;
  cursor: default;
  font-size: 20px;
  &:hover{
    color: green;
    outline: 3px solid #222;
    z-index: 3;
  }
}

.jumbo {
  width: 160px;
}

.outputScreen{
  font-size: 29px;
